import React, {useState} from "react";
import {FormControl, Typography} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import {api} from "../../../remote_api/entrypoint";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
import Stepper from "@material-ui/core/Stepper";
import "./Forms.css";
import "./CreateScopeForm.css";
import TextField from "@material-ui/core/TextField";

export default function CreateScopeForm() {

    const [showTrackerCode, setShowTrackerCode] = useState(false);
    const [scope, setScope] = useState('');
    const [trackerApiUrl, setTrackerApiUrl] = useState('http://localhost:8181');

    const onScopeChange = (ev) => {
        setScope( ev.target.value);
    }

    const onTrackerApiChange = (ev) => {
        setTrackerApiUrl( ev.target.value);
    }

    const handleSubmitClick = () => {
        api({"Content-type": "application/json"})
            .post('scope', {"name": scope, "host": trackerApiUrl})
            .then(response => {
                setShowTrackerCode(true);
                handleNext();
            })
            .catch((e) => {
                setShowTrackerCode(false);
            });
    }

    const TrackerCode = () => {
        if(showTrackerCode) {
            return <React.Fragment>
                <Typography variant="h6">Source created</Typography>
                <div className="ScopeDesc" style={{marginTop:20}}>Please find tracker javascript code in your scope tracker tab and paste it in your head tag of your web page. </div>

            </React.Fragment>
        }
        return "";
    }

    function getSteps() {
        return ['Type scope', 'Type context server api host', 'Tracker code'];
    }

    const [activeStep, setActiveStep] = React.useState(0);
    const steps = getSteps();
    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };
    const buttons = () => {
        if(activeStep < steps.length - 2)
            return <React.Fragment>
                {activeStep !== 0 && <Button
                    disabled={activeStep === 0}
                    onClick={handleBack}
                    className="Button"
                >
                    Back
                </Button>}
                <Button
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    className="Button">
                    Next
                </Button>
            </React.Fragment>
    }

    const getStepContent = (step) => {

        switch (step) {
            case 0:
                return <React.Fragment>
                    <div className="ScopeDesc">
                        Type scope name. Source is usually a namespace for all your events and it is
                        associated with your file or business activity.
                    </div>
                    <div style={{marginBottom: 20, width:"80%"}}>
                        <TextField
                            onChange={onScopeChange}
                            value={scope}
                            label="Source"/>
                    </div>
                </React.Fragment>
            case 1:
                return <React.Fragment>
                    <div className="ScopeDesc">
                        Tracker API URL is a host name and port where the context server api is located. Usually it is http://localhost:8181 or
                        for dockerized platforms http://context-server:8181.
                    </div>
                    <div style={{marginBottom: 20, width:"80%"}}>
                        <TextField
                            onChange={onTrackerApiChange}
                            label="Tracker API"
                            value={trackerApiUrl}
                        />
                    </div>

                    <Button onClick={handleSubmitClick}
                            variant="contained"
                            color="primary">
                        Submit
                    </Button>

                </React.Fragment>
            case 2:
                return <TrackerCode/>

            default:
                return 'Unknown step';
        }
    }

    return <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((label, index) => (
            <Step key={label} onClickCapture={()=>{setActiveStep(index)}}>
                <StepLabel>{label}</StepLabel>
                <StepContent>
                    <FormControl>{getStepContent(index)}</FormControl>
                    <div>
                        <div>
                            {buttons()}
                        </div>
                    </div>
                </StepContent>
            </Step>
        ))}
    </Stepper>

}