import React from 'react';
import {DataGrid} from '@material-ui/data-grid';
import {connect} from "react-redux";
import {showAlert} from "../../../../redux/reducers/alertSlice";

export function RuleDataGrid({onClick, data}) {

    const columns = [
        {
            field: 'scope',
            headerName: 'Source',
            width: 100
        },{
            field: 'name', headerName: 'Name',
            flex: 1,
            renderCell: (params) => {
                return (
                    <span style={{textDecoration:"underline", cursor:"pointer"}} onClickCapture={() => onClick(params.getValue('ruleId'))} >
                        {params.value}

                    </span>
                )}
        }
    ];

    return <DataGrid rows={data} rowHeight={35} pageSize={20} columns={columns}/>

}

const mapProps = () => {
    return {}
}

export default connect(
    mapProps,
    {
        showAlert,
    },
)(RuleDataGrid)
