import React from "react";
import theme from "../../../themes/inspector_light_theme";
import {ObjectInspector} from "react-inspector";
import "../lists/cards/ScopeCard.css";
import "./RuleDetails.css";
import Switch from "@material-ui/core/Switch";
import createMuiTheme from "@material-ui/core/styles/createMuiTheme";
import {ThemeProvider} from "@material-ui/styles";
import DetailBox from "./DetailBox";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import TabPanel from "../tabs/TabPanel";
import {MiniHeader} from "../Headers";
import DetailHeader from "./DetailHeader";
import {Button} from "@material-ui/core";
import "./Details.css";
import ConfirmationDialog from "../misc/ConfirmationDialog";

export default function SegmentDetails({data, onDelete}) {

    const [tab, setTab] = React.useState(0);
    const [openConfirmation, setOpenConfirmation] = React.useState(false);

    const handleTabChange = (event, newValue) => {
        setTab(newValue);
    };

    const switchTheme = createMuiTheme({
        typography: {
            body1: {
                fontSize: 14,
            }
        }
    })

    const SwitchRow = ({label, value, handleChange}) => {
        return <div className="RuleSwitch">
            <div className="RuleSwitchLabel">{label}</div>
            <Switch checked={value} onChange={handleChange}/>
        </div>
    }

    const UqlDetails = ({data}) => {
        if (data.meta.synchronized === true) {
            return <React.Fragment>
                <MiniHeader>WHEN</MiniHeader>
                <DetailBox>
                    {data.doc.condition}
                </DetailBox>

                <MiniHeader>UQL create statement</MiniHeader>
                <DetailBox>
                    {data.doc.uql}
                </DetailBox>
            </React.Fragment>
        }
    }

    function a11yProps(index) {
        return {
            id: `tab-${index}`,
            'aria-controls': `tabpanel-${index}`,
        };
    }

    const onClick = () => {
        setOpenConfirmation(true);
    }

    const onConfirmedDelete = () => {
        setOpenConfirmation(false);
        onDelete(data.meta.id);
    }

    const onDeleteClose = () => {
        setOpenConfirmation(false);
    }

    return <div style={{height: "inherit"}}>
        <DetailHeader label={data.meta.name}/>
        <Tabs value={tab} onChange={handleTabChange} variant="fullWidth">
            <Tab label="Meta" {...a11yProps(0)} />
            <Tab label="UQL" {...a11yProps(1)} disabled={!data.meta.synchronized}/>
            <Tab label="Unomi" {...a11yProps(2)} />
        </Tabs>
        <div className="RightTabScroller" style={{height: "calc(100% - 150px)"}}>
            <TabPanel value={tab} index={0}>

                <MiniHeader>description</MiniHeader>
                <DetailBox>{data.meta.description}</DetailBox>

                <MiniHeader>scope</MiniHeader>
                <DetailBox>
                    {data.meta.scope}
                </DetailBox>

                <MiniHeader>metadata</MiniHeader>
                <ThemeProvider theme={switchTheme}>
                    <SwitchRow label="Enabled" value={data.meta.enabled}/>
                    <SwitchRow label="Read only" value={data.meta.readOnly}/>
                    <SwitchRow label="Hidden" value={data.meta.hidden}/>
                </ThemeProvider>

                <MiniHeader>tags</MiniHeader>
            </TabPanel>
            <TabPanel value={tab} index={1}>
                <UqlDetails data={data}/>
            </TabPanel>
            <TabPanel value={tab} index={2}>
                <MiniHeader>GET /segment/{data.meta.id}</MiniHeader>
                <DetailBox>
                    <ObjectInspector data={data} theme={theme} expandLevel={3}/>
                </DetailBox>
            </TabPanel>
        </div>
        <div className="DetailActionButtons">
            <Button onClick={onClick} variant="outlined" color="primary">
                Delete
            </Button>
            <ConfirmationDialog open={openConfirmation} title="Do you want to delete this segment?"
                                content="This action can not be undone." onClose={onDeleteClose}
                                onAgree={onConfirmedDelete}
            />
        </div>
    </div>

}