import React from "react";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import PropTypes from 'prop-types';
import Popover from "@material-ui/core/Popover";
import {makeStyles, useTheme} from '@material-ui/core/styles';
import AddIcon from "@material-ui/icons/Add";
import Fab from "@material-ui/core/Fab";
import SwipeableViews from 'react-swipeable-views';
import Box from '@material-ui/core/Box';
import CreateRuleForm from "./forms/CreateRuleForm";
import CreateSegmentForm from "./forms/CreateSegemntForm";
import CreateScopeForm from "./forms/CreateScopeForm";
import {ThemeProvider} from "@material-ui/styles";
import {plusPopOverTheme} from "../../themes";
import "./CreatePopOver.css";

function TabPanel(props) {
    const {children, value, index, ...other} = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`scrollable-auto-tabpanel-${index}`}
            aria-labelledby={`scrollable-auto-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={0}>
                    {children}
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `scrollable-auto-tab-${index}`,
        'aria-controls': `scrollable-auto-tabpanel-${index}`,
    };
}

export default function CreatePopOver() {

    const useStyles = makeStyles((theme) => ({
        tabs: {
            flexGrow: 1,
            width: '700px',
            position: "fixed",
            zIndex: 2,
            backgroundColor: "#2196f3",
        },
        fab: {
            position: 'fixed',
            bottom: theme.spacing(4),
            right: theme.spacing(4),
        },
        popOver: {
            marginTop: "48px",
            padding: "10px 30px 10px 0px",
            width: "700px",
            height: "350px",
        }
    }));
    const classes = useStyles();
    const theme = useTheme();

    const [anchorEl, setAnchorOfCreatePopover] = React.useState(null);
    const [tabValue, setTabValue] = React.useState(0);
    const open = Boolean(anchorEl);
    const id = open ? 'create-popover' : undefined;

    const handlePlusClose = () => {
        setAnchorOfCreatePopover(null);
    };

    const handlePlusOpen = (event) => {
        setAnchorOfCreatePopover(event.currentTarget);
    };

    const handleTabChange = (event, newValue) => {
        setTabValue(newValue);
    };

    return <React.Fragment>
        <Fab color="secondary" aria-label="add" className={classes.fab}>
            <AddIcon onClick={handlePlusOpen}/>
            <ThemeProvider theme={plusPopOverTheme}>
                <Popover
                    id={id}
                    open={open}
                    anchorEl={anchorEl}
                    onClose={handlePlusClose}
                    anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                    transformOrigin={{
                        vertical: 525,
                        horizontal: 'right',
                    }}
                    elevation={3}
                >
                    <div  className="CreatePopOver">
                        <div className="PopOverTabs">
                            <Tabs
                                value={tabValue}
                                onChange={handleTabChange}
                                indicatorColor="secondary"
                                textColor="secondary"
                                variant="scrollable"
                                scrollButtons="auto"
                                aria-label="scrollable auto tabs"
                            >
                                <Tab label="Source" {...a11yProps(0)}/>
                                <Tab label="Rule" {...a11yProps(1)}/>
                                <Tab label="Segement" {...a11yProps(2)}/>
                            </Tabs>
                        </div>
                        <div className="PopOverTabContent">
                                <TabPanel value={tabValue} index={0}>
                                    <CreateScopeForm/>
                                </TabPanel>
                                <TabPanel value={tabValue} index={1}>
                                    <CreateRuleForm/>
                                </TabPanel>
                                <TabPanel value={tabValue} index={2}>
                                    <CreateSegmentForm/>
                                </TabPanel>
                        </div>
                    </div>

                </Popover>
            </ThemeProvider>
        </Fab>
    </React.Fragment>
}