import React from "react";
import "./AppBox.css";
import AppBar from "./AppBar";
import {connect, useDispatch} from "react-redux";
import LinearProgress from "@material-ui/core/LinearProgress";
import makeStyles from "@material-ui/core/styles/makeStyles";
import CreatePopOver from "./elements/CreatePopOver";
import {runQuery} from "../redux/reducers/uqlSlice"

const AppBox = ({progressBar, children, title, showSearch}) => {

    const dispatch = useDispatch()
    const useStyles = makeStyles((theme) => ({
        root: {
            width: '100%',
        },
        progress: {
            background: 'transparent',
        },
    }));
    const classes = useStyles();

    const query = (query) => {
        dispatch(runQuery({"query":query}))
    }

    return <div style={{height:"100%", display: "flex", flexDirection: "column"}}>
        <AppBar runQuery={query} title={title} showSearch={showSearch}/>
        <div className="contentPane">
            <div className={classes.root}>
                <LinearProgress className={classes.progress} color="secondary" variant="determinate" value={progressBar.progress}/>
            </div>
            <div className="content">
                {children}
            </div>
        </div>
        <CreatePopOver></CreatePopOver>
    </div>
}

const mapProps = (state) => {
    return {
        progressBar: state.progressReducer,
    }
}

export default connect(mapProps)(AppBox)