import React from 'react';
import {fade, makeStyles} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import Badge from '@material-ui/core/Badge';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import NotificationsIcon from '@material-ui/icons/Notifications';
import {
    WarningOutlined,
    AccountBox
} from '@material-ui/icons';

import "./AppBar.css";
import {connect} from "react-redux";
import {useHistory} from "react-router-dom";
import Tooltip from "@material-ui/core/Tooltip";
import Zoom from "@material-ui/core/Zoom";
import Drawer from "./Drawer";
import RightPaper from "./elements/RightPaper";

const useStyles = makeStyles((theme) => ({
    grow: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },

    title: {
        color: 'primary',
        display: 'none',
        [theme.breakpoints.up('sm')]: {
            display: 'block',
        },
        "& span": {
            fontWeight: 300
        }
    },
    thin: {
        fontWeight: '300'
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginRight: theme.spacing(2),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(3),
            width: 'auto',
            '&:focus': {
                width: '100%',
            },
        },
    },
    searchIcon: {
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '20ch',
            '&:focus': {
                width: '70ch',
            },
        },
    },
    sectionDesktop: {
        display: 'none',
        [theme.breakpoints.up('md')]: {
            display: 'flex',
        },
    },
    sectionMobile: {
        display: 'flex',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    drawerPaper: {
        width: "280px",
        color: theme.palette.background.default
    },
    drawerItem: {
        root: {
            minWidth: "10px"
        }
    },
    menuItem: {
        color: 'black'
    }
}));

const TopBar = ({runQuery, notificationCount, warningIcon, title, showSearch, rightPaper}) => {
    const classes = useStyles();

    const [drawerState, setDrawerState] = React.useState(false);
    const history = useHistory()

    const _logout = () => {
        history.push('/app/logout')
    };

    const toggleDrawer = (toggle) => (event) => {
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setDrawerState(toggle);
    };

    const handleKeyPress = (ev) => {
        if (ev.key === 'Enter') {
            runQuery(ev.target.value);
            ev.preventDefault();
        }
    };

    const displayWarningIcon = () => {
        return warningIcon.show && (<Tooltip title={warningIcon.message} TransitionComponent={Zoom}>
            <IconButton aria-label="show alert" color="inherit">
                <WarningOutlined/>
            </IconButton>
        </Tooltip>)
    }

    const displaySearch = () => {
        if(showSearch) {
            return <div className={classes.search}>
                <div className={classes.searchIcon}>
                    <SearchIcon/>
                </div>
                <InputBase
                    placeholder="Search"
                    classes={{
                        root: classes.inputRoot,
                        input: classes.inputInput,
                    }}
                    inputProps={{'aria-label': 'search'}}
                    onKeyPress={handleKeyPress}/>
            </div>
        }
    }

    return (
        <div className={classes.grow}>
            <Drawer toggleDrawer={toggleDrawer} drawerState={drawerState}/>
            <RightPaper open={rightPaper.open}/>
            <AppBar position="fixed" style={{background:"linear-gradient(45deg, #1565c0 30%, #5e35b1 90%)"}}>
                <Toolbar>
                    <IconButton
                        edge="start"
                        className={classes.menuButton}
                        color="inherit"
                        aria-label="open drawer"
                        onClick={toggleDrawer(true)}
                    >
                        <MenuIcon/>
                    </IconButton>
                    <Typography className={classes.title} variant="h6" noWrap>
                        TRACARDI <span>{title}</span>
                    </Typography>
                    {displaySearch()}
                    <div className={classes.grow}/>
                    <div className={classes.sectionDesktop}>
                        {displayWarningIcon()}
                        <Tooltip title="Notifications" TransitionComponent={Zoom}>
                            <IconButton aria-label="show new notifications" color="inherit">
                                <Badge badgeContent={notificationCount} color="secondary">
                                    <NotificationsIcon/>
                                </Badge>
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Logout" TransitionComponent={Zoom}>
                            <IconButton aria-label="Logout" color="inherit" onClick={_logout}>
                                <AccountBox/>
                            </IconButton>
                        </Tooltip>
                    </div>
                    <div className={classes.sectionMobile}>
                        <Tooltip title="Logout" TransitionComponent={Zoom}>
                            <IconButton aria-label="Logout" color="inherit" onClick={_logout}>
                                <AccountBox/>
                            </IconButton>
                        </Tooltip>
                    </div>
                </Toolbar>
            </AppBar>
        </div>
    );
}

const mapProps = (state) => {
    return {
        notificationCount: state.notificationReducer.value,
        warningIcon: state.warningIconReducer,
        rightPaper: state.rightPaperReducer
    }
}
export default connect(mapProps)(TopBar);