import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import Avatar from '@material-ui/core/Avatar';
import "./ProfileCard.css";


const useStyles = makeStyles(() => ({
    root: {
        width: "100%",
        cursor: "pointer",
        maxHeight: 70,
        background: "white",
        borderBottom: "solid 1px #ddd"
    },
}));

export default function ProfileCard({data, onClick}) {
    const classes = useStyles();

    return (
        <Card className={classes.root}>
            <CardHeader
                avatar={
                    <Avatar aria-label="profile" className="ProfileAvatar">P</Avatar>
                }
                title={data.profileId}
                subheader={<span style={{fontWeight: 300}}>{data.systemProperties.lastUpdated}</span>}
                onClick={(ev)=>{onClick(data.profileId)}}
            />
        </Card>
    );
}
