import {loading, ready, failed} from "../redux/reducers/datasetSelectSlice"

import {api} from "./entrypoint";
import {showAlert} from "../redux/reducers/alertSlice";

export const fetchUQL = (uqlStatement) => dispatch => {
    dispatch(loading());
    try {
        const path = '/console/select';
        api({"Content-type": "text/plain"})
            .post(path, uqlStatement)
            .then(response => {
                dispatch(ready({"data": response.data}));
            })
            .catch((e) => {
                dispatch(failed(e.message));
                if (typeof (e.response) != "undefined") {
                    dispatch(showAlert({type: "error", message: `Query failed with error ${e.response.data.detail}`}));
                } else {
                    dispatch(showAlert({type: "error", message: e.message}));
                }
            });
    } catch (e) {
        dispatch(failed(e.message))
    }
};

export const fetchData = (uqlStatement, url, setLoading, setError, setReady, setDefaultValues=true) => {
    if(setDefaultValues) {
        setError(false);
        setReady(false);
        setLoading(true);
    }
    try {
        api({"Content-type": "text/plain"})
            .post(url, uqlStatement)
            .then(response => {
                setLoading(false);
                setReady({"data": response.data});
            })
            .catch((e) => {
                setLoading(false);
                if (typeof (e.response) != "undefined") {
                    setError(e.response.data.detail);
                } else {
                    setError(e.message);
                }
            });
    } catch (e) {
        setError(e.message);
    }
};

