export const createRule = (params, loading, ready, error) => {
    try {
        loading();
        const path = '/rule';
        api({"Content-type": "application/json"})
            .post(path, params)
            .then(response => {
                ready({"data": response.data});
            })
            .catch((e) => {
                error(e);
            });
    } catch (e) {
        error(e);
    }
};

