import dot from "dot-object";
import DetailKeyValue from "./DetailKeyValue";
import React from "react";

export default function Properties({properties}) {

    function empty(obj) {
        return obj  && Object.keys(obj).length === 0 && obj.constructor === Object
    }

    const dotted = dot.dot(properties);
    const keyValues = () => Object.entries(dotted).map(
        ([label, value]) => {
            return <DetailKeyValue key={label} label={label} value={value === null || empty(value) ? 'null or {}' : value.toString()}/>
        }
    )
    return <React.Fragment>
        {keyValues()}
    </React.Fragment>
}