import {connect} from "react-redux";
import React from "react";
import {getData} from "../../remote_api/uql_api_endpoint";
import ListDetailView from "../elements/ListDetailView";
import FilterListWrapper from "../elements/FilterListWrapper";
import ScopeCard from "../elements/lists/cards/ScopeCard";
import ScopeDetails from "../elements/details/ScopeDetails";
import "./Page.css";

const Sources = () => {

    const [loading, setLoading] = React.useState(false);
    const [error, setError] = React.useState(false);
    const [ready, setReady] = React.useState(false);


    const onClick = (id) => {
        getData('/scope/'+id, setLoading, setError, setReady)
    }

    const details = () =>  {
        if (ready != false) {
            return <ScopeDetails data={ready.data}/>
        }
    }

    const blockFilteredList = () => (<FilterListWrapper
        endPoinyUrl="/scope/select"
        renderList={
            (data) => {
                return data.map((row, index) => (
                    <ScopeCard key={index} data={row} onClick={onClick}/>
                ))
            }
        }
        filterLabel="Type scope name to filter"
        filterHelper='Example: my-site-1'
        filterKey="filterScopeQuery"
    />);

    return <div className="PagePane">
        <ListDetailView list={blockFilteredList} detail={details}/>
    </div>
}

const mapProps = (state) => {
    return {
        notification: state.notificationReducer,
    }
}
export default connect(
    mapProps,
)(Sources)
