import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import PrivateRoute from "./authentication/PrivateRoute";
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import AppBox from "./AppBox";
import Console from "./pages/Console";
import {connect, useDispatch} from "react-redux";
import {hideAlert} from "../redux/reducers/alertSlice";
import Logout from "./authentication/Logout";
import SignIn from "./authentication/SignIn";
import Events from "./pages/Events";
import Rules from "./pages/Rules";
import Segments from "./pages/Segments";
import Sources from "./pages/Sources";
import Profiles from "./pages/Profiles";
import ManualPage from "./pages/manual/ManualPage";

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const App = ({alert}) => {

    const dispatch = useDispatch()

    const handleClose = () => {
        dispatch(hideAlert())
    };

    return (
        <Router>
            <Switch>
                <Route exact path="/app/login">
                    <SignIn/>
                </Route>
                <Route exact path="/app/logout">
                    <Logout/>
                </Route>
                <PrivateRoute exact path="/app/sources" roles={["unomi"]}>
                    <AppBox title="Sources" showSearch={false}>
                        <Sources/>
                    </AppBox>
                </PrivateRoute>
                <PrivateRoute exact path="/app/events" roles={["unomi"]}>
                    <AppBox title="Events" showSearch={false}>
                        <Events/>
                    </AppBox>
                </PrivateRoute>
                <PrivateRoute exact path="/app/rules" roles={["unomi"]}>
                    <AppBox title="Rules" showSearch={false}>
                        <Rules/>
                    </AppBox>
                </PrivateRoute>
                <PrivateRoute exact path="/app/segments" roles={["unomi"]}>
                    <AppBox title="Segments" showSearch={false}>
                        <Segments/>
                    </AppBox>
                </PrivateRoute>
                <PrivateRoute exact path="/app/profiles" roles={["unomi"]}>
                    <AppBox title="Profiles" showSearch={false}>
                        <Profiles/>
                    </AppBox>
                </PrivateRoute>
                <PrivateRoute exact path="/app/console" roles={["unomi"]}>
                    <AppBox title="Console" showSearch={true}>
                        <Console/>
                    </AppBox>
                </PrivateRoute>
                <PrivateRoute exact path="/app" roles={["unomi"]}>
                    <AppBox title="Console" showSearch={true}>
                        <Console/>
                    </AppBox>
                </PrivateRoute>

                <Route path="/app/manual">
                    <AppBox title="Manual" showSearch={true}>
                        <Route exact path="/app/manual/uql/select">
                                <ManualPage mdFile="uql/select"/>
                        </Route>
                        <Route exact path="/app/manual/uql/create">
                                <ManualPage mdFile="uql/create"/>
                        </Route>
                        <Route exact path="/app/manual/uql/delete">
                                <ManualPage mdFile="uql/delete"/>
                        </Route>
                        <Route exact path="/app/manual">
                            <ManualPage mdFile="start"/>
                        </Route>
                    </AppBox>
                </Route>

            </Switch>
            <Snackbar open={alert.show} autoHideDuration={alert.hideAfter} onClose={handleClose}>
                <Alert onClose={handleClose} severity={alert.type} >
                    <pre style={{margin:0,overflow: "auto", maxWidth:"500px"}}>{alert.message}</pre>
                </Alert>
            </Snackbar>
        </Router>
    );
}

const mapState = (state) => {
    return {
        alert: state.alertReducer
    }
}
export default connect(mapState)(App)
