import React from "react";
import theme from "../../../themes/inspector_light_theme";
import {ObjectInspector} from "react-inspector";
import "../lists/cards/ScopeCard.css";
import "./RuleDetails.css";
import DetailBox from "./DetailBox";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import TabPanel from "../tabs/TabPanel";
import {MiniHeader} from "../Headers";
import DetailHeader from "./DetailHeader";
import "./Details.css";
import Properties from "./DetailProperties";

export default function ProfileDetails({data}) {

    const [tab, setTab] = React.useState(0);

    const handleTabChange = (event, newValue) => {
        setTab(newValue);
    };

    function a11yProps(index) {
        return {
            id: `tab-${index}`,
            'aria-controls': `tabpanel-${index}`,
        };
    }

    return <div style={{height: "inherit"}}>
        <DetailHeader label={data.itemId}/>
        <Tabs value={tab} onChange={handleTabChange} variant="fullWidth">
            <Tab label="Meta" {...a11yProps(0)} />
            <Tab label="Properties" {...a11yProps(1)}   />
            <Tab label="Unomi" {...a11yProps(2)} />
        </Tabs>
        <div className="RightTabScroller">
            <TabPanel value={tab} index={0}>
                <MiniHeader>Version</MiniHeader>
                <DetailBox>{data.version}</DetailBox>
            </TabPanel>
            <TabPanel value={tab} index={1}>
                <MiniHeader>Properties</MiniHeader>
                <Properties properties={data.properties}/>

                <MiniHeader>System Properties</MiniHeader>
                <Properties properties={data.systemProperties}/>

                <MiniHeader>Segments</MiniHeader>
                <Properties properties={data.segments}/>
            </TabPanel>
            <TabPanel value={tab} index={2}>
                <MiniHeader>GET /profile/{data.itemId}</MiniHeader>
                <DetailBox>
                    <ObjectInspector data={data} theme={theme} expandLevel={3}/>
                </DetailBox>
            </TabPanel>
        </div>
    </div>;

}