import React from "react";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import {makeStyles} from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import {FormControl} from "@material-ui/core";
import FormHelperText from "@material-ui/core/FormHelperText";
import {createSegment} from "../../../remote_api/uql_api_endpoint";
import Alert from "@material-ui/lab/Alert";
import AlertTitle from "@material-ui/lab/AlertTitle";
import "./CreateRuleForm.css"
import KeyValueFragment from "../misc/KeyValueFragment";

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%'
    },
    button: {
        marginTop: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    actionsContainer: {
        marginBottom: theme.spacing(2),
    },
    resetContainer: {
        paddingLeft: theme.spacing(3),
        paddingRight: theme.spacing(3),
        paddingBottom: theme.spacing(3),
    },
    formControl: {
        margin: theme.spacing(1),
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
        marginRight: theme.spacing(2),
        minWidth: "100px"
    },
}));

function getSteps() {
    return ['Select scope and segment name', 'Type segemnt description', 'Create condition', 'Submit segment'];
}

function SegmentConditionFragment({conditionValue, handleConditionChange}) {

    return <React.Fragment>
        <div className="description">
            <b>Condition</b> is SQL like condition that describes when the segment should be triggered.
            Please refer to documentation to see the syntax of conditions. Segment condition is <b>required field</b>.
        </div>
        <TextField
            required
            id="seg-condition"
            label="Segment condition"
            multiline
            value={conditionValue}
            onChange={handleConditionChange}
        />
        <FormHelperText>Example: (scope="my-site1" OR scope="my-site2") AND event:type="order"</FormHelperText>
    </React.Fragment>
}

function SegmentDescFragment({descValue, handleDescChange}) {
    return <React.Fragment>
        <div className="description">
            <b>Segment description</b> can be any string that describes the meaning of segment.
            For example the segment can be a group of frequent customers. Segment description is <b>required field</b>.
        </div>
        <TextField id="seg-desc"
                   label="Segemnt description"
                   value={descValue}
                   onChange={handleDescChange}/>
        <FormHelperText>Example: Frequent customers who made purchase at least 3 time a month.</FormHelperText>
    </React.Fragment>
}

function SegmentNameFragment({nameValue, handleNameChange, scope, handleScopeChange}) {
    const classes = useStyles();
    return <React.Fragment>
        <div className="description">
            Set <b>scope and segment name</b>. <b>Source</b> is an id of your site and <b>segment</b> name can be any
            string that identifies segment. Segment name is <b>required field</b>.
            Segemnt id is made out of segment name by replacing spaces with hyphens and lowering the string.
        </div>
        <div style={{display: "flex"}}>
            <div style={{verticalAlign: "bottom"}}>
                <Select
                    value={scope}
                    onChange={handleScopeChange}
                    inputProps={{
                        name: 'scope',
                        id: 'scope-select',
                    }}
                    className={classes.selectEmpty}
                >
                    <MenuItem value="">
                        <em>None</em>
                    </MenuItem>
                    <MenuItem value={"site-1"}>site-1</MenuItem>
                    <MenuItem value={"site-2"}>site-2</MenuItem>
                    <MenuItem value={"site-3"}>site-3</MenuItem>
                </Select>
                <FormHelperText>Source</FormHelperText>
            </div>
            <div><TextField required id="seg-name" label="Segment name"
                            value={nameValue}
                            onChange={handleNameChange}/>
                <FormHelperText>Example: Frequent customers</FormHelperText>
            </div>
        </div>
    </React.Fragment>
}

function SubmitSegment({scope, name, desc, condition, handleReset}) {
    const classes = useStyles();

    const [resetDisabled, setResetDisabled] = React.useState(false);
    const [submitDisabled, setSubmitDisabled] = React.useState(false);
    const handleResetDisabled = (toggle) => {
        setResetDisabled(toggle);
    };
    const handleSubmitDisabled = (toggle) => {
        setSubmitDisabled(toggle);
    };

    const [submitResult, setSubmitResult] = React.useState('');
    const [submitResultType, setSubmitTypeResult] = React.useState('');
    const handleSubmitResult = (message) => {
        setSubmitResult(message);
    };
    const handleSubmitTypeResult = (type) => {
        setSubmitTypeResult(type);
    };

    const handleSubmitClick = () => {
        let params = {
            "scope": scope,
            "name": name,
            "desc": desc,
            "condition": condition,
        }

        const ok = (response) => {
            handleSubmitTypeResult("success");
            handleSubmitResult("Segment created.");
            handleSubmitDisabled(false)
        }

        const failed = (e) => {
            handleSubmitDisabled(false)
            handleSubmitTypeResult("error");
            if (typeof (e.response) != "undefined") {
                handleSubmitResult(e.response.data.detail);
            } else {
                handleSubmitResult(e.toString());
            }
        }

        const loading = () => {
            handleSubmitDisabled(true)
            handleSubmitTypeResult("loading");
            handleSubmitResult("Loading");
        }

        createSegment(params, loading, ok, failed);
    }

    const handleResetClick = () => {
        handleReset();
        handleSubmitDisabled(false)
    };

    const submitStatus = (message, type) => {
        if (message !== "") {
            return <Alert severity={type} style={{marginTop: 20}}>
                <AlertTitle>{type.toUpperCase()}</AlertTitle>
                <pre style={{width: "515px", overflow: "auto"}}>{message}</pre>
            </Alert>
        }
    }

    return <React.Fragment>
        <Typography>All steps completed - here is your segment summary</Typography>
        <div style={{marginTop: 20}}>
            <KeyValueFragment name={"Source"} value={scope}/>
            <KeyValueFragment name={"Name"} value={name}/>
            <KeyValueFragment name={"Description"} value={desc}/>
            <KeyValueFragment name={"Condition"} value={condition}/>
        </div>
        {submitStatus(submitResult, submitResultType)}
        <div style={{marginTop: 10}}>
            <Button onClick={handleSubmitClick} className={classes.button} variant="contained"
                    color="primary" disabled={submitDisabled}>
                Submit
            </Button>
            <Button onClick={handleResetClick} className={classes.button} disabled={resetDisabled}>
                Back
            </Button>
        </div>

    </React.Fragment>
}


export default function CreateSegmentForm() {
    const classes = useStyles();
    const [activeStep, setActiveStep] = React.useState(0);


    const [scope, setScope] = React.useState('');
    const handleScopeChange = (event) => {
        setScope(event.target.value);
    };

    const [conditionValue, setConditionValue] = React.useState('');
    const handleConditionChange = (event) => {
        setConditionValue(event.target.value);
    };

    const [nameValue, setNameValue] = React.useState('');
    const handleNameChange = (event) => {
        setNameValue(event.target.value);
    };

    const [descValue, setDescValue] = React.useState('');
    const handleDescChange = (event) => {
        setDescValue(event.target.value);
    };

    const steps = getSteps();

    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleReset = () => {
        handleBack()
    }

    const getStepContent = (step) => {

        switch (step) {
            case 0:
                return <SegmentNameFragment
                    nameValue={nameValue} handleNameChange={handleNameChange}
                    scope={scope} handleScopeChange={handleScopeChange}/>
            case 1:
                return <SegmentDescFragment descValue={descValue} handleDescChange={handleDescChange}/>
            case 2:
                return <SegmentConditionFragment conditionValue={conditionValue}
                                                 handleConditionChange={handleConditionChange}/>
            case 3:
                return <SubmitSegment scope={scope}
                                      name={nameValue}
                                      desc={descValue}
                                      condition={conditionValue}
                                      handleReset={handleReset}/>
            default:
                return 'Unknown step';
        }
    }
    const buttons = () => {
        if (activeStep < steps.length - 1)
            return <React.Fragment>
                {activeStep !== 0 && <Button
                    disabled={activeStep === 0}
                    onClick={handleBack}
                    className={classes.button}
                >
                    Back
                </Button>}
                <Button
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    className={classes.button}>
                    Next
                </Button>
            </React.Fragment>
    }
    return (
        <div className={classes.root}>
            <Stepper activeStep={activeStep} orientation="vertical">
                {steps.map((label, index) => (
                    <Step key={label} onClickCapture={() => {
                        setActiveStep(index)
                    }}>
                        <StepLabel>{label}</StepLabel>
                        <StepContent>
                            <FormControl className={classes.formControl}>{getStepContent(index)}</FormControl>
                            <div className={classes.actionsContainer}>
                                <div>
                                    {buttons()}
                                </div>
                            </div>
                        </StepContent>
                    </Step>
                ))}
            </Stepper>
        </div>
    );
}
