import React from "react";
import theme from "../../../themes/inspector_light_theme";
import {ObjectInspector} from "react-inspector";
import "../lists/cards/ScopeCard.css";
import Switch from "@material-ui/core/Switch";
import createMuiTheme from "@material-ui/core/styles/createMuiTheme";
import {ThemeProvider} from "@material-ui/styles";
import DetailBox from "./DetailBox";
import Tabs from "@material-ui/core/Tabs";
import TabPanel from "../tabs/TabPanel";
import {MiniHeader} from "../Headers";
import DetailHeader from "./DetailHeader";
import {fetchData} from "../../../remote_api/uql_api_endpoint";
import "./Details.css";
import "./RuleDetails.css";
import {Button} from "@material-ui/core";
import ConfirmationDialog from "../misc/ConfirmationDialog";
import {openRightPaper} from "../../../redux/reducers/rightPaperSlice";
import {connect} from "react-redux";
import CreateRuleForm from "../forms/CreateRuleForm";
import ShortTab from "../tabs/ShortTab";

function RuleDetails({data, onDelete, openRightPaper}) {

    const [tab, setTab] = React.useState(0);
    const [loading, setLoading] = React.useState(false);
    const [error, setError] = React.useState(false);
    const [ready, setReady] = React.useState(false);
    const [openConfirmation, setOpenConfirmation] = React.useState(false);

    const handleTabChange = (event, newValue) => {
        if(data && data.unomi.metadata.synchronized === true && newValue===2) {
            fetchData(data.doc.uql, '/rule/query/json', setLoading, setError, setReady);
        }
        setTab(newValue);
    };

    const switchTheme = createMuiTheme({
        typography: {
            body1: {
                fontSize: 14,
            }
        },
    })

    const SwitchRow = ({label, value, handleChange}) => {
        return <div className="RuleSwitch">
            <div className="RuleSwitchLabel">{label}</div>
            <Switch checked={value} onChange={handleChange} />
        </div>
    }

    const UqlDetails = ({data}) => {
        if(data.unomi.metadata.synchronized === true ) {
            return <React.Fragment>
                <MiniHeader>WHEN</MiniHeader>
                <DetailBox>
                   {data.doc.condition}
                </DetailBox>
                <MiniHeader>THEN</MiniHeader>
                <DetailBox>
                {data.doc.actions}
                    </DetailBox>
                <MiniHeader>UQL create statement</MiniHeader>
                <DetailBox>
                        {data.doc.uql}
                </DetailBox>
            </React.Fragment>
        }
        return <React.Fragment>
            <MiniHeader>Missing data</MiniHeader>
            <DetailBox>
                This rule was not creates with TRACARDI. You can still see metadata or Unomi data.
            </DetailBox>
        </React.Fragment>;
    }

    function a11yProps(index) {
        return {
            id: `rule-tab-${index}`,
            'aria-controls': `rule-tabpanel-${index}`,
        };
    }

    const unomiQuery = () => {
        if(ready.data) {
            return <React.Fragment>
                <MiniHeader>UNOMI API QUERY: {ready.data[1]} {ready.data[0]}</MiniHeader>
                <DetailBox>
                    <ObjectInspector data={ready.data[2]} theme={theme} expandLevel={3}/>
                </DetailBox>
            </React.Fragment>
        }
    }

    const onAddClick = () => {
        openRightPaper({content:<CreateRuleForm/>, width: 1000, anchor:"right"})
    }

    const onDeleteClick = () => {
        setOpenConfirmation(true);
    }

    const onConfirmedDelete = () => {
        setOpenConfirmation(false);
        onDelete(data.unomi.metadata.id)
    }

    const onDeleteClose = () => {
        setOpenConfirmation(false);
    }

    return <div style={{height: "inherit"}}>
        <DetailHeader label={data.unomi.metadata.name}/>
        <div>
            <Tabs value={tab} onChange={handleTabChange} variant="fullWidth">
                <ShortTab label="UQL" {...a11yProps(0)} disabled={!data.unomi.metadata.synchronized} />
                <ShortTab label="Metadata" {...a11yProps(1)} />
                <ShortTab label="Stats" {...a11yProps(2)} />
                <ShortTab label="Unomi" {...a11yProps(3)} />
            </Tabs>
        </div>

        <div className="RightTabScroller" style={{height: "calc(100% - 150px)"}}>
            <TabPanel value={tab} index={0}>
                <UqlDetails data={data} />
            </TabPanel>

            <TabPanel value={tab} index={1}>

                <MiniHeader>description</MiniHeader>
                <DetailBox>{data.unomi.metadata.description}</DetailBox>

                <MiniHeader>scope</MiniHeader>
                <DetailBox>
                    {data.unomi.metadata.scope}
                </DetailBox>

                <MiniHeader>metadata</MiniHeader>
                <ThemeProvider theme={switchTheme}>
                    <SwitchRow label="Enabled" value={data.unomi.metadata.enabled} />
                    <SwitchRow label="Read only" value={data.unomi.metadata.readOnly} />
                    <SwitchRow label="Hidden" value={data.unomi.metadata.hidden} />
                    <SwitchRow label="Missing plugins" value={data.unomi.metadata.missingPlugins} />
                </ThemeProvider>

                <MiniHeader>tags</MiniHeader>
            </TabPanel>
            <TabPanel value={tab} index={2}>
                stats
            </TabPanel>
            <TabPanel value={tab} index={3}>
                <MiniHeader>GET /rule/{data.unomi.metadata.id}/detailed</MiniHeader>
                <DetailBox>
                    <ObjectInspector data={data} theme={theme} expandLevel={3}/>
                </DetailBox>
                {unomiQuery()}
            </TabPanel>
        </div>
        <div className="DetailActionButtons">
            <Button onClick={onAddClick} color="primary">
                Add
            </Button>
            <Button onClick={onDeleteClick} variant="outlined" color="primary">
                Delete
            </Button>
            <ConfirmationDialog open={openConfirmation} title="Do you want to delete this rule?"
                                content="This action can not be undone." onClose={onDeleteClose}
                                onAgree={onConfirmedDelete}
            />
        </div>
    </div>

}

const mapProps = (state) => {
    return {
        alert: state.showAlert,
    }
}
export default connect(
    mapProps,{
        openRightPaper,
    }
)(RuleDetails)