import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CardHeader from '@material-ui/core/CardHeader';
import Avatar from '@material-ui/core/Avatar';
import { blue } from '@material-ui/core/colors';
import Card from '@material-ui/core/Card';
import "./ScopeCard.css";

const useStyles = makeStyles(() => ({
    root: {
        width: "100%",
        cursor: "pointer",
        maxHeight: 70,
        background: "white",
        borderBottom: "solid 1px #ddd"
    },
    avatar: {
        background: blue[500],
    },
}));

export default function ScopeCard({data, onClick}) {
    const classes = useStyles();

    return (
        <Card className={classes.root}>
            <CardHeader
                avatar={
                    <Avatar aria-label="scope" className="ScopeAvatar">S</Avatar>
                }
                title={data.doc.name}
                subheader={<span style={{fontWeight: 300}}>{data.doc.host}</span>}
                onClick={(ev)=>{onClick(data.doc._id)}}
            />
        </Card>
    );
}
