import React, {useEffect} from 'react';
import {DataGrid} from '@material-ui/data-grid';
import {fetchData} from "../../../../remote_api/uql_api_endpoint";
import Input from "../../forms/inputs/Input";
import {connect, useDispatch} from "react-redux";
import {showAlert} from "../../../../redux/reducers/alertSlice";
import CenteredCircularProgress from "../../progress/CenteredCircularProgress";

export function SegmentDataGrid({data, onClick}) {

    const columns = [
        {
            field: 'scope',
            headerName: 'Source',
            flex: .3,
        }, {
            field: 'name',
            headerName: 'Name',
            flex: .3,
            renderCell: (params) => {
                return (<span style={{textDecoration: "underline", cursor: "pointer"}}
                              onClickCapture={() => onClick(params.getValue('segmentId'))}>
                    {params.value}
                </span>)
            }
        },
        {
            field: 'description',
            headerName: 'Description',
            flex: .4
        }
    ];

    return <DataGrid rows={data} rowHeight={35} pageSize={20} columns={columns}/>

}

const mapProps = () => {
    return {}
}

export default connect(
    mapProps,
    {
        showAlert,
    },
)(SegmentDataGrid)
