import React from 'react';
import {DataGrid} from '@material-ui/data-grid';
import {connect} from "react-redux";
import {showAlert} from "../../../../redux/reducers/alertSlice";

export function EventDataGrid({data, onClick}) {

    const columns = [
        {
            field: 'scope',
            headerName: 'Source',
            flex: .3,
        }, {
            field: 'type',
            headerName: 'Type',
            flex: .3,
            renderCell: (params) => {
                return (<span style={{textDecoration: "underline", cursor: "pointer"}}
                              onClickCapture={() => onClick(params.getValue('eventId'))}>
                    {params.value}
                </span>)
            }
        },
        {
            field: 'timestamp',
            headerName: 'Timestamp',
            flex: .4,
            type: 'dateTime'
        }
    ];

    return <DataGrid rows={data} rowHeight={35} pageSize={20} columns={columns}/>

}

const mapProps = () => {
    return {}
}

export default connect(
    mapProps,
    {
        showAlert,
    },
)(EventDataGrid)
