import React from "react";
import Drawer from "@material-ui/core/Drawer";
import {connect} from "react-redux";
import {closeRightPaper} from "../../redux/reducers/rightPaperSlice";

function RightPaper({open, closeRightPaper, data}) {

    const content = () => {
        if(data.anchor=="right" || data.anchor=="left") {
            return <div style={{width: data.width}}>
                {data.content}
            </div>;
        } else{
            return <div style={{height: data.width}}>
                {data.content}
            </div>;
        }
    }

    return <Drawer anchor={data.anchor} open={open} onClose={closeRightPaper}>
        {content()}
    </Drawer>
}

const mapProps = (state) => {
    return {
        data: state.rightPaperReducer
    }
}

export default connect(
    mapProps,
    {closeRightPaper}
)(RightPaper)